.top {
  position: relative;
  min-height: 100vh;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &__inner {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
  }

  .slime {
    position: absolute;
    left: 50%;
    bottom: 88px;
    padding: 0;
    height: 48px;
    border: none;
    border-radius: 0;
    background-color: transparent;
    transform: translateX(-50%);
    animation: fade-link 0.8s;

    &__wrap {
      height: 20px;
    }

    &__block {
      transform: translateY(-100%);

      p {
        margin-right: 0;
        margin-left: 12px;
        font-weight: 400;
        font-size: 12px;
        line-height: 20px;
      }
    }

    &__add-inner {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 16px;
      height: 16px;
      transform: translateY(-100%);
    }

    &:hover {
      .slime__block {
        transform: translateY(0);
      }

      .slime__add-inner {
        transform: translateY(0);
      }
    }

    @include m($md) {
      bottom: 32px;
    }
  }

  h1 {
    max-width: 800px;
    text-align: center;
    text-transform: uppercase;
    animation: fade 0.5s;

    @include m($lg) {
      padding: 0 16px;
    }
  }
}

.do {
  padding: 200px 0;

  h6 {
    margin-bottom: 24px;
    animation: fade 0.8s;
  }

  ul {
    display: flex;
    flex-direction: column;
    align-items: center;

    li {
      display: flex;
      min-height: 200px;
      border-radius: 24px;
      background-color: $orange;

      &:not(:last-child) {
        margin-bottom: 24px;
      }
    }

    .slide-item {
      width: calc((100vw - (100vw - 1200px)) / 2);
      overflow: hidden;

      @media (max-width: 1400px) {
        width: calc((100vw - (100vw - 1248px)) / 2);
      }

      @media (max-width: 1248px) {
        width: calc((100vw - 48px) / 2);
      }
    }

    .do__main {
      position: relative;
      z-index: 5;
      display: flex;
      flex: 0 0 calc((100vw - (100vw - 1200px)) / 2);
      align-items: flex-end;
      justify-content: space-between;
      padding: 40px;
      width: calc((100vw - (100vw - 1200px)) / 2);
      overflow: hidden;
      border-radius: 24px;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;

      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.48) 0%, rgba(0, 0, 0, 0) 100%);
      }

      h5 {
        z-index: 5;
      }

      img {
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      @media (max-width: 1400px) {
        flex: 0 0 calc((100vw - (100vw - 1248px)) / 2);
        width: calc((100vw - (100vw - 1248px)) / 2);
      }

      @media (max-width: 1248px) {
        flex: 0 0 calc((100vw - 48px) / 2);
        width: calc((100vw - 48px) / 2);
      }
    }

    .do__info {
      display: flex;
      flex: 0 0 calc((100vw - (100vw - 1200px)) / 2);
      flex-direction: column;
      justify-content: center;
      padding: 46px 40px 40px;
      width: calc((100vw - (100vw - 1200px)) / 2);

      img {
        margin-bottom: 16px;
        width: 32px;
        height: 32px;
      }

      p {
        font-size: 14px;
        line-height: 22px;
      }

      @media (max-width: 1400px) {
        flex: 0 0 calc((100vw - (100vw - 1248px)) / 2);
        width: calc((100vw - (100vw - 1248px)) / 2);
      }

      @media (max-width: 1248px) {
        flex: 0 0 calc((100vw - 48px) / 2);
        width: calc((100vw - 48px) / 2);
      }
    }

    .do-red {
      background-color: $red;

      .link__service {
        span {
          background-color: $red;
        }
      }
    }

    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }
}

.trusted {
  position: relative;
  padding: 120px 0;
  overflow: hidden;
  background-color: $gray-dark;

  &__list {
    animation: fade 0.8s;
  }

  .section__title {
    p {
      @include m($lg) {
        margin-bottom: 4px;
      }
    }
  }

  .section__head {
    h5 {
      span {
        @include m($md) {
          margin-right: 8px;
        }
      }

      @include m($md) {
        position: absolute;
        left: 16px;
        bottom: 80px;
        padding-bottom: 0;
      }
    }

    @include m($lg) {
      margin-bottom: 40px;
    }
  }

  ul {
    display: flex;
    width: 100vw;
    animation: trusted 12s linear infinite;

    li {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 -24px 0 0;
      width: calc(20vw + 24px);
      height: calc(20vw + 24px);
      min-width: calc(20vw + 24px);
      min-height: calc(20vw + 24px);
      max-width: calc(20vw + 24px);
      max-height: calc(20vw + 24px);
      border-style: solid;
      border-width: 1px;
      border-color: rgba($white, 0.24);
      border-radius: 50%;

      img {
        @include m($lg) {
          max-width: 80px;
        }

        @include m($md) {
          max-width: 70px;
        }
      }
    }
  }

  @include m($xl) {
    padding: 100px 0;
  }

  @include m($lg) {
    padding: 80px 0;
  }

  @include m($lg) {
    padding: 80px 0 140px;
  }
}

.company {
  position: relative;
  padding: 120px 0;
  background-image: url("../images/company.svg");
  background-position: top left -200px;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: $black;

  .section__head {
    margin-bottom: 90px;

    @include m($xl) {
      margin-bottom: 60px;
    }

    @include m($lg) {
      margin-bottom: 40px;
    }
  }

  .slime {
    @include m($md) {
      position: absolute;
      left: 50%;
      bottom: 72px;
      transform: translateX(-50%);
    }
  }

  &__list {
    display: grid;
    grid-column-gap: 24px;
    grid-row-gap: 48px;
    grid-template-columns: 1fr 1fr 1fr;
    margin-left: auto;
    max-width: 588px;
    animation: fade 0.8s;

    li {
      width: 100%;
      max-width: 180px;
    }

    h3 {
      margin-bottom: 4px;

      @include m($lg) {
        font-size: 40px;
        line-height: 48px;
      }
    }

    @include m($md) {
      margin-left: 0;
      max-width: 100%;
    }

    @include m($sm) {
      grid-column-gap: 16px;
      grid-row-gap: 48px;
      grid-template-columns: 1fr 1fr;
    }
  }

  @include m($md) {
    background-image: none;
  }

  @include m($xl) {
    padding: 100px 0;
  }

  @include m($lg) {
    padding: 72px 0;
  }

  @include m($md) {
    padding: 72px 0 168px;
  }
}

.studies {
  position: relative;
  padding: 200px 0;
  overflow: hidden;
  border-radius: 0 0 40px 40px;
  background-color: $gray-dark;

  .section__head {
    margin-bottom: 48px;

    h2 {
      font-weight: 500;
    }

    @include m($lg) {
      margin-bottom: 37px;
    }
  }

  .swiper-slide {
    max-width: 384px;

    @include m(600) {
      max-width: 300px;
    }
  }

  .slime {
    border: 1px solid $gray-medium;

    @include m($md) {
      position: absolute;
      left: 50%;
      bottom: 80px;
      transform: translateX(-50%);
    }
  }

  @include m($xl) {
    padding: 150px 0;
  }

  @include m($lg) {
    padding: 80px 0;
  }

  @include m($md) {
    padding: 80px 0 176px;
  }
}

.touch {
  margin-top: -40px;
  padding: 160px 0 120px;
  background-image: linear-gradient(252.56deg, #6c57ff 26.71%, #ff5366 149.39%);
  border-radius: 24px;
  overflow: hidden;
  min-height: calc(100vh - 74px);
  h2 {
    margin-bottom: 16px;
    font-size: 64px;
    line-height: 72px;
    //animation: fade 0.8s;

    @include m($xl) {
      font-size: 56px;
      line-height: 64px;
    }

    @include m($lg) {
      margin-bottom: 8px;
      font-size: 48px;
      line-height: 56px;
    }
  }

  &__vac {
    display: flex;
    flex-direction: column;
    margin-right: 24px;
    max-width: 384px;

    h2 {
      margin-bottom: 24px !important;

      @include m($xl) {
        margin-bottom: 16px !important;
      }

      @include m($lg) {
        margin-bottom: 8px !important;
      }
    }

    p {
      font-weight: 600;
    }

    @include m($xl) {
      max-width: 350px;
    }

    @include m($lg) {
      margin: 0 0 40px;
      max-width: 100%;
    }
  }

  h4 {
    margin-bottom: 72px;
    //animation: fade 0.8s;

    @include m($xl) {
      margin-bottom: 56px;
    }

    @include m($lg) {
      margin-bottom: 40px;
    }
  }

  h1 {
    margin-bottom: 16px;

    @include m($lg) {
      margin-bottom: 8px;
    }
  }

  &__place {
    li {
      &:not(:last-child) {
        margin: 0 0 64px;

        @include m($lg) {
          margin: 0 0 0 41px;
        }
      }
    }

    p {
      font-size: 14px;
      line-height: 22px;

      &:not(:last-child) {
        margin-bottom: 24px;

        @include m($lg) {
          margin-bottom: 24px;
        }
      }
    }

    @include m($lg) {
      display: flex;
      flex-direction: row-reverse;
      justify-content: flex-end;
    }

    @include m($lg) {
      margin-bottom: 32px;
    }
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    //animation: fade 0.8s;

    @include m($lg) {
      flex-direction: column;
    }
  }

  h6 {
    margin-bottom: 8px;
  }

  &__tel {
    display: block;
    margin-bottom: 4px;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.04em;

    @include m($xl) {
      font-size: 20px;
      line-height: 28px;
    }

    @include m($lg) {
      font-size: 18px;
      line-height: 26px;
    }
  }

  .form {
    width: 100%;
    max-width: 792px;

    p {
      margin-bottom: 32px;
      font-size: 12px;
      line-height: 20px;

      @include m($md) {
        margin-bottom: 24px;
      }
    }

    label {
      position: relative;
      margin: 0;
      width: 100%;

      span {
        position: absolute;
        left: 0;
        top: 0;
        display: block;
        margin-bottom: 8px;
        font-weight: 400;
        font-size: 12px;
        line-height: 20px;
        color: $gray;
      }

      input {
        padding-bottom: 16px;
        width: 100%;
        outline: none;
        border: none;
        border-bottom: 1px solid $white;
        font-weight: 600;
        font-size: 14px;
        line-height: 24px;
        color: $white;
        background-color: transparent;
      }

      textarea {
        margin-bottom: 20px;
        padding: 16px;
        width: 100%;
        height: 122px;
        outline: none;
        border: 1px solid $white;
        background-color: transparent;
        resize: none;

        &::placeholder {
          font-weight: 600;
          font-size: 14px;
          line-height: 24px;
          color: $white;
        }
      }
    }

    .textarea {
      padding-top: 28px;
      color: $white;
    }

    &__block {
      display: flex;
      justify-content: space-between;
      margin-bottom: 40px;

      label:first-child {
        margin: 0 24px 0 0;

        @include m($md) {
          margin: 0 0 40px;
        }
      }

      @include m($md) {
        flex-direction: column;
      }
    }

    &__button {
      position: relative;
      margin-left: auto;
      max-width: fit-content;
      border-radius: 32px;

      .slime {
        padding: 0 32px;
        border: none;
        background-color: $white;

        p {
          margin: 0;
          font-weight: 600;
          font-size: 16px;
          line-height: 24px;
          color: $orange;
        }
      }

      input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        border-radius: 32px;
      }

      &:hover {
        cursor: pointer;
      }
    }

    @include m($xl) {
      max-width: 700px;
    }

    @include m($lg) {
      max-width: 100%;
    }
  }

  @include m($xl) {
    padding: 130px 0 100px;
  }

  @include m($lg) {
    padding: 111px 0 80px;
  }
}

@keyframes trusted {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}

.swiper__studies {
  margin-right: -34%;
  overflow: visible;
  animation: fade 0.8s;

  @include m(1400) {
    margin-right: 0;
  }
}

.contact {
  margin-top: 0;
  padding: 160px 0;
  background-image: none;
  background-color: $black;

  h1 {
    margin-bottom: 24px;

    @include m($lg) {
      margin-bottom: 16px;
    }
  }

  &__wrap {
    p {
      margin-bottom: 16px;
      font-weight: 400;
    }

    a {
      text-decoration: underline;
      color: $orange;
    }
  }

  &__vac {
    .touch__place {
      margin-top: 16px;

      @include m($lg) {
        margin-top: 8px;
        margin-bottom: 40px;
      }
    }

    h6 {
      @include m($lg) {
        margin-bottom: 8px;
      }
    }

    @include m($xl) {
      max-width: 350px;
    }

    @include m($lg) {
      margin-right: 0;
      max-width: 100%;
    }
  }

  @include m($xl) {
    padding: 120px 0;
  }

  @include m($lg) {
    padding: 80px 0;
  }
}